
import React, { useState, useEffect } from 'react';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

const KanbanView = () => {
  const [workflows, setWorkflows] = useState([]);

  useEffect(() => {
    fetch('/api/kanban')
      .then(response => response.json())
      .then(data => setWorkflows(data));
  }, []);

  const handleDragEnd = (result) => {
    if (!result.destination) return;

    const { source, destination } = result;

    const sourceWorkflow = workflows.find(workflow =>
      workflow.steps.some(step => step.step_id === parseInt(source.droppableId))
    );
    const destinationWorkflow = workflows.find(workflow =>
      workflow.steps.some(step => step.step_id === parseInt(destination.droppableId))
    );

    const sourceStep = sourceWorkflow.steps.find(step => step.step_id === parseInt(source.droppableId));
    const destinationStep = destinationWorkflow.steps.find(step => step.step_id === parseInt(destination.droppableId));

    const [movedDocument] = sourceStep.documents.splice(source.index, 1);
    destinationStep.documents.splice(destination.index, 0, movedDocument);

    setWorkflows([...workflows]);

    // Update the document's step_id in the backend
    fetch(`/api/document_workflow/${movedDocument.document_id}`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        step_id: destinationStep.step_id,
      }),
    });
  };

  return (
    <DragDropContext onDragEnd={handleDragEnd}>
      <div className="kanban-board">
        {workflows.map((workflow) => (
          <div key={workflow.workflow_id}>
            <h2>{workflow.workflow_name}</h2>
            <div className="workflow">
              {workflow.steps.map((step) => (
                <Droppable droppableId={String(step.step_id)} key={step.step_id}>
                  {(provided) => (
                    <div
                      className="kanban-step"
                      {...provided.droppableProps}
                      ref={provided.innerRef}
                    >
                      <h3>{step.step_name}</h3>
                      {step.documents.map((document, index) => (
                        <Draggable key={document.document_id} draggableId={String(document.document_id)} index={index}>
                          {(provided) => (
                            <div
                              className="kanban-document"
                              ref={provided.innerRef}
                              {...provided.draggableProps}
                              {...provided.dragHandleProps}
                            >
                              <p>{document.title}</p>
                            </div>
                          )}
                        </Draggable>
                      ))}
                      {provided.placeholder}
                    </div>
                  )}
                </Droppable>
              ))}
            </div>
          </div>
        ))}
      </div>
    </DragDropContext>
  );
};

export default KanbanView;