import React from 'react';
import { BrowserRouter as Router, Route, Routes, Link } from 'react-router-dom';
import WorkflowView from './WorkflowView';  // Import your existing WorkflowView
import KanbanView from './KanbanView';  // Import your KanbanView component
import { ErrorBoundary } from 'react-error-boundary';  // Error boundary for safe error handling

// Error fallback component to display more useful information
const ErrorFallback = ({ error, resetErrorBoundary }) => (
  <div role="alert">
    <h2>Something went wrong:</h2>
    <pre>{error.message}</pre>
    <button onClick={resetErrorBoundary}>Try again</button>
  </div>
);

const App = () => (
  <ErrorBoundary FallbackComponent={ErrorFallback}>
    <Router>
      <div className="App">
        {/* Navigation links */}
        <nav>
          <ul>
            <li>
              <Link to="/react-page">Workflow Timeline</Link>
            </li>
            <li>
              <Link to="/kanban-page">Kanban Board</Link>
            </li>
          </ul>
        </nav>

        {/* Define routes for different views */}
        <Routes>
          {/* Route for Workflow Timeline */}
          <Route path="/react-page" element={<WorkflowView />} />

          {/* Route for Kanban Board */}
          <Route path="/kanban-page" element={<KanbanView />} />

          {/* Default route or a 404 page */}
          <Route path="*" element={<h1>404 Not Found</h1>} />
        </Routes>
      </div>
    </Router>
  </ErrorBoundary>
);

export default App;
